.footer__text {
  color: #545454;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 568px) {
  .footer__text {
    font-size: 14px;
    line-height: 17px;
  }
}
